/// Loading indicator for the form
/// @group Elements
.loading {
  margin: 0 auto;
  width: 3em;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;

  div {
    width: 0.5em;
    height: 0.5em;
    border-radius: 50%;
    background-color: var(--colorPrimaryTrans);

    &:nth-of-type(1) {
      transform: translateX(-100%);
      animation: left-swing 0.5s ease-in alternate infinite;
    }

    &:nth-of-type(3) {
      transform: translateX(-95%);
      animation: right-swing 0.5s ease-out alternate infinite;
    }

    @media (min-aspect-ratio: 50/100) {
    }

    @media (min-aspect-ratio: 75/100) {
    }

    @media (min-aspect-ratio:80/100) {
    }

    @media (min-aspect-ratio: 90/100) {
    }

    @media (min-aspect-ratio: 125/100) {
    }
  }

  /// Animations
  @keyframes left-swing {

    50%,
    100% {
      transform: translateX(95%);
    }
  }

  @keyframes right-swing {
    50% {
      transform: translateX(-95%);
    }

    100% {
      transform: translateX(100%);
    }
  }
}